/* Main settings */
body {
  background-color:#F4F4F4;
  text-align: center;
  font-family:"Helvetica";}

.mainContainer {color:#212121;}

/* Logo */
.logo {
  background-color:#EBEBEB;
  position:fixed;
  margin: 0 auto;
  width:100%;
  z-index:1;}
.logo h1 {text-shadow:2px 2px #cccccc;}
.logo h5 {color:#B1B1B1;
margin-bottom:15px;}

/* Getting rid of ugly margin below expanded div */
.Panel {
  position: fixed;
  margin: 5% auto;
  left: 0;
  right: 0;
  margin-top:132px;
  width:80%;
  border:none;
  -webkit-box-shadow:2px 2px #E0DFDF;
          box-shadow:2px 2px #E0DFDF;
  z-index:2;}
.PanelInside {
  font-size:12px;
  text-align:left;
  padding-left:4%;
  padding-right:4%;
  padding-top:20px;
  padding-bottom:10px;
  line-height:24px;}

/* Button [INSTRUCTIONS] */
.Button {
  width:100%;
  height:20px;
  background-color:#E6E6E6;
  border: 1px solid #E1E1E1;
  color:#9D9D9D !important;
  font-size:11px;
  line-height:8px;}
.Button:focus {
  outline:none !important;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  border: 1px solid #E0E0E0 !important;}
.Button:active {
  border: 1px solid #E0E0E0 !important;
  background-color:#E1E1E1 !important;}
.Button:hover {
  border: 1px solid #E0E0E0;}

/* Grid */

.Grid {
  display:block;
  margin: 0 auto;
  line-height:9px;
  margin-top:10px;
  padding:0;
}

.BoxOn {
  width:15px;
  height:15px;
  border:1px solid #C7C7C7;
  display:inline-block;
  background-color:green;
  }

.BoxOff {
  width:15px;
  height:15px;
  border:1px solid #C7C7C7;
  display:inline-block;
  }

.BoxOff:hover {
  background-color:lightblue;
}

/*Button Toolbar*/

.ButtonContainer {
  display:inline-block;
  margin: 0 auto;
  margin-top:150px;
}

.generationContainer {
  margin-top:10px;
  font-size:20px;
  color:grey;
}

.copyright {
  font-size:12px;
  color:lightgrey;
}


